  font-family: 'PMNCaeciliaSansHead';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___})
    format('truetype');
  font-weight: 100;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'PMNCaeciliaSansHead', sans-serif;
  text-decoration: none;
  font-weight: normal;
}

.sidebar {
  background-color: var(--sidebar-background-color);
  padding: 5px;
  padding-top: 140px;
  width: 25%;
  height: 100vh; /* Set full viewport height */
  position: sticky !important;
  top: 0; /* Ensure it sticks to the top */
  overflow-y: auto; /* Enable scrolling if content overflows */
}

.sidebarStart {
  display: flex;
  align-items: center;
  padding: 20px 0;
  cursor: pointer;
  margin-left: 10px;
}

.sidebarStart::before {
  content: '';
  position: fixed;
  left: 0;
  top: 18%;
  transform: translateY(-50%);
  width: 0.8%;
  height: 3.8rem;
  background-color: var(--primarycolor);
}

.sidebarStart span {
  font-size: medium;
  margin-top: 12px;
}

.homeIcon {
  width: 30px;
  height: 25px;
  margin-right: 10px;
  margin-left: 10px;
  fill: var(--secondarycolor);
  margin-top: 12px;
}

.angledownIcon {
  width: 25px;
  height: 30px;
  margin-right: 8px;
  margin-left: 50px;
  fill: var(--secondarycolor);
}

.sidebarStart span {
  font-size: 20px;
  color: var(--primarycolor);
  text-decoration: none;
}

.sidebarMenu {
  display: flex;
  padding: 5px 0;
  margin-left: 20px;
  cursor: pointer;
}

.menuIcon {
  fill: var(--secondary-color);
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.sidebarMenu span {
  font-size: 20px;
  color: var(--secondary-color);
  transition: color 0.3s ease;
}

.sidebarMenu span:hover {
  color: var(--primarycolor);
}

.submenu span {
  font-size: 16px;
  padding-right: 10px;
  transition: color 0.3s ease;
}

.submenu span:hover {
  color: var(--primarycolor);
}

.submenu {
  display: none;
  flex-direction: column;
  margin-top: 20px;
  padding-left: 5px;
  font-size: 15px;
  color: var(--secondary-color);
}

.sidebarMenu.show .submenu {
  display: flex;
}

.sidebar-button-container {
  display: none;
  flex-direction: column;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background: var(--primarycolor);
  color: var(--sidebar-background-color);
  padding: 8px;
  text-decoration: none;
  z-index: 1000;
}

.skip-link:focus {
  top: 0;
}
.sidebarOverlay {
  position: fixed;
  inset: 0;
  z-index: 40;
  background-color: black;
  opacity: 50%;
}

/* Responsive styles */
@media (max-width: 1200px) {
  .sidebar {
    display: none;
    position: fixed;
    z-index: 100;
  }

  .sidebar.visible {
    display: block;
  }

  .sidebar-button-container {
    display: flex;
  }
}

@media (max-width: 1000px) {
  .sidebarStart span,
  .sidebarMenu span,
  .submenu span {
    font-size: 14px;
  }
}
