___CSS_LOADER_EXPORT___.push([module.id, `.new-cert-form {
  margin: 0 auto;
  margin-top: 140px;
  padding: 20px;
  width: 100%;
  max-width: 1200px;
  background-color: var(--sidebar-background-color);
  border-radius: 10px;
}

.form-container {
  display: flex;
  justify-content: space-between;
}

.left-side,
.right-side {
  width: 49%;
}

.form-input-container {
  margin-bottom: 20px;
}

.form-input-label {
  display: block;
  margin-bottom: 5px;
  font-weight: small;
  font-style: italic;
}

.form-input {
  width: 100%;
  padding: 10px;
  border: 1px solid var(--border-color);
}

.form-input-multiple {
  display: flex;
  border: thin solid var(--border-color);
}

.form-input-multiple div {
  display: flex;
  align-items: center;
  padding: 0 10px;
  background-color: var(--title-color);
}

.input-icon {
  cursor: pointer;
}

.form-input-select {
  appearance: none;
  padding: 10px;
  border: 1px solid var(--border-color);
  background-color: var(--background-color);
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}); /* Adds custom arrow icon */
  background-repeat: no-repeat;
  background-position: right 10px top 50%;
  background-size: 16px 16px;
}

.right-side {
  margin-left: 80px;
}

.upload-btn {
  padding: 9px 50px;
  background-color: var(--primarycolor);
  color: var(--title-color);
  border: none;
  cursor: pointer;
  margin-bottom: 0.5rem;
}

.upload-btn:hover {
  background-color: var(--light-blue);
}

.upload-input {
  display: none;
}

.file-preview-panel {
  width: 100%;
  height: 500px;
  border: 1px solid var(--border-color);
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

.file-preview-panel iframe {
  display: flex;
  width: 100%;
  height: 500px;
}

.buttons-container {
  display: flex;
  justify-content: flex-end;
  margin-top: 20px;
}

.submit-cert-btn {
  padding: 10px 50px;
  background-color: var(--button-color);
  color: var(--background-color);
  border: none;
  cursor: pointer;
  margin-right: 10px;
}

.submit-cert-btn:hover {
  background-color: var(--button-color);
}

.reset-cert-btn {
  padding: 10px 50px;
  color: rgb(0, 0, 0);
  border: none;
  cursor: pointer;
}

.mt-1 {
  margin-top: 2rem;
}
.mb-1 {
  margin-bottom: 1rem;
}
.btn {
  display: flex;
  align-items: left; /* Align items vertically (center them) */
  justify-content: left; /* Align items horizontally */
}

.btn svg {
  margin-right: 5px; /* Add some space between the icon and the text */
}
